body {
  background-color: #eee;
}
.App {
  width: 80%;
  margin: 0 auto;
  margin-top: 70px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  color: #000;
}
.todo {
  border: 1px solid rgb(102, 73, 73);
  padding: 10px;
  margin-bottom: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

button {
  border: 1px solid #666;
  background-color: transparent;
  font-weight: bold;
}

.btn-primary {
  background-color: #bada55;
}

.btn-default {
  background-color: #eee;
}
.todo:hover {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.5);
}
.todo.complete {
  background-color: rgba(0, 0, 0, 0.1);
}
.app-input-group {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.app-input-group input {
  width: 55%;
  height: 40px;
  font-size: 24px;
  border: 1px solid #666;
  padding: 0;
  padding-left: 10px;
}
.app-input-group button {
  width: 20%;
  height: 42px;
}
.todo span {
  margin-left: 10px;
}

@media all and (min-width: 1024px) {
  .App {
    width: 50%;
  }
}

@media all and (max-width: 768px) {
  .app-input-group {
    flex-wrap: wrap;
  }
  .app-input-group input {
    width: 100%;
  }

  .app-input-group button {
    margin-top: 10px;
    width: 45%;
  }
}
